		// 	header:{
		// 		'content-type':'application/x-www-form-urlencoded'
		// 	},
		// 	data:{
		// 		qq: this.qq
		// 	},
		// 	success: (res) => {
		// 		if (res.data.status == 200) {
		// 			var hotSuperheroList = res.data.data;
		// 			this.hotSuperheroList = hotSuperheroList;
		// 		}
		// 	}
		// });
		uni.request({
			url: common.localUrl + '/movie/hot?type=superhero',
			method: "POST",
			data:{
				type: 'superhero'
			},
			success: (res) => {
				if (res.data.code === 0) {
