		// 			var hotTrailerList = res.data.data;
		// 			this.hotTrailerList = hotTrailerList;
		// 		}
		// 	}
		// });
		uni.request({
			url: common.localUrl + '/movie/hot?type=trailer',
			method: "POST",
			data:{
				type: 'trailer'
			},
			success: (res) => {
				if (res.data.code === 0) {
					var hotTrailerList = res.data.data;
					this.hotTrailerList = hotTrailerList;
				}
			}
		});
		
		this.refresh()
	},
	onPullDownRefresh() {
		this.refresh()
	},
	methods: {
		refresh(){
			uni.showLoading({	//开启loading
				mask:true
			})
			uni.showNavigationBarLoading()	//开启导航栏loading
			//查询猜你喜欢数据列表
			// uni.request({
			// 	url: common.serverUrl + '/index/guessULike',
			// 	method: "POST",
			// 	header:{
			// 		'content-type':'application/x-www-form-urlencoded'
			// 	},
			// 	data:{
			// 		qq: this.qq
			// 	},
			// 	success: (res) => {
			// 		if (res.data.status == 200) {
			// 			var guessULikeList = res.data.data;
			// 			this.guessULikeList = guessULikeList;
			// 		}
			// 	},
			// 	complete() {
			// 		uni.hideLoading()
			// 		uni.hideNavigationBarLoading()	//关闭导航栏loading
			// 		uni.stopPullDownRefresh()	//停止下拉刷新
			// 	}
			// })
			uni.request({
				url: common.localUrl + '/movie/guessULike',
				method: "get",
				success: (res) => {
					if (res.data.code === 0) {
						var guessULikeList = res.data.data;
						this.guessULikeList = guessULikeList;
					}
				},
				complete() {
					uni.hideLoading()
					uni.hideNavigationBarLoading()	//关闭导航栏loading
					uni.stopPullDownRefresh()	//停止下拉刷新
				}
			})
		},
		showTraller(e){
			var trailerId = e.currentTarget.dataset.trailerid;
			//页面跳转api
			uni.navigateTo({
				url:"../movie/movie?trailerId="+trailerId
			})
			// uni.switchTab({
			// 	url:"../me/me"
			// })
		},
		//实现点赞动画效果
		praiseMe(e){

			var gIndex = e.currentTarget.dataset.gindex
			//构建动画数据，并且通过step来表示这组动画的完成
			this.animation.translateY(-60).opacity(1).step({
				duration: 400
			});
			// 导出动画数据到view组件，实现组件的动画效果
			// this.animationData = this.animation.export();
			this.animationData = this.animation;
			this.animationDataArr[gIndex] = this.animationData.export();
			
			// 还原动画
			setTimeout(()=> {
				this.animation.translateY(0).opacity(0).step({
					duration: 100
				});
				this.animationData = this.animation;
				this.animationDataArr[gIndex] = this.animationData.export();
			}, 1000);

		},
		//播放一个视频时，需要暂停其他正在播放的视频
		meIsPlaying(e){
			var me = this;
			var trailerId = "";
			if (e) {
				trailerId = e.currentTarget.dataset.playindex;
				me.videoContext = uni.createVideoContext(trailerId);
			}
			var hotTrailerList = me.hotTrailerList;
			for(var i = 0; i< hotTrailerList.length ;i++){
				var tempId = hotTrailerList[i].id;
				if(tempId != trailerId) {
					uni.createVideoContext(tempId).pause()
				}
			}
		}
	},
	onHide() {
		if(this.videoContext){
			this.videoContext.pause()
		}
	},
	components: {
		helloComp,
		trailerStars
	},
}
